/* Reset básico */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  font-family: 'Roboto', sans-serif;
  background-color: #f9f9f9;
  color: #333;
  line-height: 1.6;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  list-style: none;
}

button {
  cursor: pointer;
}

/* Variáveis globais */
:root {
  --primary-color: #4CAF50;
  --secondary-color: #FF9800;
  --background-color: #ffffff;
  --text-color: #333333;
  --border-color: #ddd;
}
